        //push class "on" into the iframe of the clicked video 
        //make variable prvVid to class 'on' of the first clicked video
    //else - qs("on")? - .on exists
        //prvVid.src = prvVid.src //stop last vid
        //remove .on from .on and add .on to Etarget
//if user clicks a non youtube tab && prvVid.includes("youtube") (so it has a link)
    //prvVid.src = prvVid.src //stop last vid
   // freqsweep,leftright, musicvid,pong 

    var prvVid = ''; //last video the user was watching
    var trgtIsAv = (eTarget) => eTarget.parentElement.parentElement.parentElement.children[0].classList.contains("Audio&Vision");
    export default function ytAudioChk(tabType,eTarget){

        debugger;

        if(eTarget.innerHTML == "Audio"){

        }

        //if user clicks into youtube video tab
        if(trgtIsAv(eTarget) == true /*&& eTarget.dataset.toolType != undefined*/){
            switch(document.querySelector(".av-tool.on") == null){
                //if a video hasn't been watched yet, add .on to the tab
                case true:
                    if(prvVid == ''){
                        if(tabType.includes("Sync")){ document.querySelector(".av-tool.sync").classList.add("on") }
                        else if(tabType.includes("Sweep")){ document.querySelector(".av-tool.freqsweep").classList.add("on") }
                        else if(tabType.includes("Music")){ document.querySelector(".av-tool.musicvid").classList.add("on") }
                        else if(tabType.includes("Left")){ document.querySelector(".av-tool.leftright").classList.add("on") }
                        
                        prvVid = document.querySelector(".av-tool.on");
                    }
                    break;
                //if a video has been watched 
                case false:
                    if(prvVid != ''){
                        prvVid.src = prvVid.src //stop last vid, 
                        document.querySelector(".av-tool.on").classList.remove("on"); 

                        if(tabType.includes("Sync")){ document.querySelector(".av-tool.sync").classList.add("on") }
                        else if(tabType.includes("Sweep")){ document.querySelector(".av-tool.freqsweep").classList.add("on") }
                        else if(tabType.includes("Music")){ document.querySelector(".av-tool.musicvid").classList.add("on") }
                        else if(tabType.includes("Left")){ document.querySelector(".av-tool.leftright").classList.add("on") }

                        prvVid = document.querySelector(".av-tool.on");
                        break;
                    }
            }
        }
        //if a user has watched a video and is clicking a non AV tab (eg rgb of vision), stop the video and remove .on from said videos iframe
        else if(prvVid.src != undefined){ 
            if(prvVid.src.includes("youtube")){
                prvVid.src = prvVid.src //stop last vid, 
                document.querySelector(".av-tool.on").classList.remove("on"); //remove on from prv tab and add .on to new tab 
                prvVid = '';
            }
        }
    }
