import React from 'react'

class Info_Instructions extends React.Component {
    constructor(props){
        super(props);
        this.state = { }
    }

    render() {

        var smallArticle = {
          marginBottom: "-100px"
        }

        //hide previous article and turn on new btn / turn off prv btn 
        function configInfo(btn,classes){
          debugger;
          if(btn.classList.contains("off")){
            //hide previous displayed article 
            ["off","display-none"].forEach( item => document.querySelector("article.info.on").classList.add(item) )
            document.querySelector("article.info.on").classList.remove("on");

            //display clicked article
            document.querySelector(classes).classList.remove("display-none");
            document.querySelector(classes).classList.add("on");
        
            //toggle select button as appropriate
            document.querySelector(".info.btn.on").classList.add("off"); document.querySelector(".info.btn.on").classList.remove("on");
            btn.classList.remove("off"); btn.classList.add("on");
          }
        }
        //devipher which button/article to hide/display
        function infoFunct(e){

          var clkdBtn = (e.target.dataset.type != undefined) ? e.target : e.target.parentElement; //make clkdBtn always equal the actual button, not any inner content (eg span)

            switch(clkdBtn.dataset.type){
              case "audio": 
                configInfo(clkdBtn,".audio.info")
                break;
              case "vision":
                configInfo(clkdBtn,".vision.info")
                break;
              case "AV":
                configInfo(clkdBtn,".av.info")
                break;
          }
        }
        
      return (
          <div>
              <div className=" info-instructions-wrap tool-element-wrap display-none">
                
                <div className="info-instructions-article-wrap">
                  {/* audio article */}
                  <article className="info info-instructions-article audio on">
                    <div className="instructions-blob-wrap sg" style={smallArticle}>
                      <h3 className="audio instructions-header sg">Signal Generator</h3>
                      <p className="audio instructions-p sg">
                        Choose if you want a constant tone or a tone sweep test(low to high or high to low),
                        pick your source tone, then if youre using a constant tone, 
                        decide on the frequency spectrum you wish to hear (this will be disabled in sweep mode).
                      </p>
                    </div>
                    <div lassName="instructions-blob-wrap rp">
                      <h3 className="audio instructions-header rp">Recording Playback</h3>
                      <p className="audio instructions-p rp">
                        Choose between 10 sounds of both music and voice to play
                      </p>
                    </div>
                  </article>
                  {/* vision article */}
                  <article className="info info-instructions-article vision display-none off">
                    <div className="instructions-blob-wrap gs-rgb" style={smallArticle}>
                      <h3 className="audio instructions-header gs-rgb" >Greyscale &#38; RGB</h3>
                      <p className="audio instructions-p gs-rgb">
                        Turn your monitor into either a greyscale or RGB color pattern. Click the screen to enter into full screen mode
                      </p>
                    </div>

                    <div className="instructions-blob-wrap solid-color">
                      <h3 className="audio instructions-header solid-color">Solid Color</h3>
                      <p className="audio instructions-p solid-color">
                      Test your monitor by inidivually viewing all the colors from the RGB pattern in solid fullscreen format,
                      Hold your mouse on the screen for 3 seconds to enter/exit fullscreen mode and single click to change color
                      </p>
                    </div>
                  </article>
                  {/* av article */}
                  <article className="info info-instructions-article av audio&#38;vision display-none off">
                    <div className="instructions-blob-wrap music-video">
                      <h3 className="audio instructions-header music-video">Music Video</h3>
                      <p className="audio instructions-p music-video"> 
                      Sit back and enjoy a classic music video while testing your screen and audio system
                      </p>
                    </div>

                    <div className="instructions-blob-wrap freq-visualiser">
                      <h3 className="audio instructions-header freq-visualiser">Frequency Visualiser</h3>
                      <p className="audio instructions-p freq-visualiser"> 
                      Run a full spectrum audio frequency sine sweep test with visuals to guide you of where the current frequency is at
                      </p>
                    </div>

                    <div className="instructions-blob-wrap gravity-ball">
                      <h3 className="audio instructions-header gravity-ball">Sync test</h3>
                      <p className="audio instructions-p gravity-ball">
                      Audio video sync test to help adjust latency between speakers and sync your video and audio
                      </p>
                    </div>

                    <div className="instructions-blob-wrap left-right">
                      <h3 className="audio instructions-header left-right">Left-right audio test</h3>
                      <p className="audio instructions-p left-right">
                      Left right speaker test, to make sure your speakers are capable of stereo audio 
                      </p>
                    </div>
                  </article>
                </div>

                <div className="info-instructions-btn-wrap" onClick={(e) => infoFunct(e)}>
                  <button className="btn info info-instructions-btn audio on" data-type="audio" style={{marginLeft: "25px"}}><span>Audio</span></button>
                  <button className="btn info info-instructions-btn vision off" data-type="vision" style={{margin: "0 25px"}}><span>Vision</span></button>
                  <button className="btn info info-instructions-btn Audio&#38;Vision av off" data-type="AV" style={{marginRight: "25px"}}><span>Audio &#38; Vision</span></button>
                </div>

              </div>
          </div>
      );
    }
  }


  

  export {Info_Instructions}