import React from 'react'
import {makeSelectedNav} from '../Components/Tools/codeTools'
import {updateCurrentState} from './NavBarJsFunctions/DisplaySwitching'

//DROPDOWN NAVIGATION FOR NAVBAR NAV CHILDREN (EG RGB OF VISION)
class DropdownNav extends React.Component {
    constructor(props){
        super(props);
        this.state = {}
        this.clsDrpDwn = this.clsDrpDwn.bind(this);
        this.liConfig = this.liConfig.bind(this);
    }

    clsDrpDwn(){
      document.querySelector(".drp-dwn-active-lmnt").classList.add("display-none");
      document.querySelector(".drp-dwn-active-lmnt").classList.remove("drp-dwn-active-lmnt");
      if(document.querySelector(".drp-dwn-active")) document.querySelector(".drp-dwn-active").classList.remove("drp-dwn-active");
    }

    liConfig(e){
      //debugger;
      updateCurrentState(e); //update state object (state of where navigation is at + display switching)
      makeSelectedNav(e); this.clsDrpDwn(); document.querySelector("#adaptive-nav-wrap").style.right == "" ?
      document.querySelector("#adaptive-nav-wrap").style = "right: 0px !important" : 
      document.querySelector("#adaptive-nav-wrap").style = "";
      document.querySelector(".adptv-nav-tab").style = "";
    }

//the the list count depend on how many how many li's there will be, so how many ddrowndown menu options
    render() {
      var _adptvNavWrap = document.querySelector("#adaptive-nav-wrap");
      //iterate over the <dropDownNav/> props and turn them into an array
      //later to be mapped through and returned as li for designated drop down
      var propsArray = [];
      for (var key of Object.keys(this.props)){
        propsArray.push(this.props[key]);
      }

      return (
          <div className="drp-dwn-nav-wrap display-none">
            <ul className="drp-dwn-nav">
              {propsArray.map( (option, index) => (
                <li className="drp-dwn-child unselected tab-cls-trgr" data-tool-type={propsArray[index]} key={index} onClick={(e) => this.liConfig(e) }> {option} 
                </li>
              ))}
            </ul>
          </div>
      );
    }
  }
  export { DropdownNav } ;