import {noSpace,displayNoneToggle,makeSelectedNav} from '../Tools/codeTools'
//variables for both functions
var prvSlctdNav = ''; //refers to previously selected navigation parent

//code for adptvTabFunction
function adptvTabCode(e){

    //prvSelectedNav equates to the dropdown wrap
    const _adptvNavWrap = document.querySelector("#adaptive-nav-wrap");
    const adptvTab = document.querySelector(".adptv-nav-tab");
    // CLOSED => OPEN
    if (_adptvNavWrap.classList.contains("closed")) {
        _adptvNavWrap.style="right: 0px !important"; 
        _adptvNavWrap.classList.remove("closed");
        _adptvNavWrap.classList.add("opened");
        adptvTab.style= "right: 140px"
    }
    //OPEN => CLOSED (closes via audio button or other child buttons (eg vision nav child))
    else if (e.target.innerText == "Audio" || e.target.classList.contains("tb-cls-trgr")){
        _adptvNavWrap.style = "";
        setTimeout(() => {
            _adptvNavWrap.style = "z-index: -100; visibility: hidden;";
        }, 500); 
        _adptvNavWrap.classList.remove("opened");
        _adptvNavWrap.classList.add("closed");
        adptvTab.style= ""

        //if closed via adptv tab and a dropdown has been selected
        if(prvSlctdNav != '' && e.target.classList.contains("adptv-nav-tab")) {
            if(!prvSlctdNav.parentElement.children[0].children[0].classList.contains("Audio")){
                //hide all navs with "" selected (incase user has one important tab and is browsing another)
                document.querySelectorAll(".selected").forEach( (item) => {
                    item.classList.remove("selected");
                    item.classList.add("unselected");
                });
                if (document.querySelector(".drp-dwn-active-lmnt")) document.querySelector(".drp-dwn-active-lmnt").classList.remove("drp-dwn-active-lmnt");
            }
            if (!prvSlctdNav.classList.contains("display-none"))displayNoneToggle(prvSlctdNav); 
            prvSlctdNav = '';                        
        }

        //if audio is clicked with another menu already open
        if(e.target.innerText == "Audio" && document.querySelector(".drp-dwn-active-lmnt")){
            document.querySelector(".drp-dwn-active-lmnt").classList.add("display-none");
        }
    }

    //open-close dropdown
    if(e.target.classList.contains("tool-name") && e.target.innerText != "Audio"){
        //if another drp down is already open, close that drop down (general purpose)
        if(prvSlctdNav != '' && e.target.parentElement.classList.contains("unselected") && document.querySelector(".drp-dwn-active-lmnt")){

            //I THINK U CAN DELETE THIS
            //if in a responsive view and user has just come back to nav from clicking a nav & clicks prv nav parent
            // don't displayNoneToggle
            // if( document.querySelector("#adaptive-nav-wrap").classList.contains("opened")
            //  && document.querySelector(".selectedImportant") != null && document.querySelector(".drp-dwn-active-lmnt") != null){
            //     //don't do anything (for now)
            // } else {
            //     displayNoneToggle(prvSlctdNav);
            // }

            document.querySelector(".drp-dwn-active-lmnt").classList.remove("drp-dwn-active-lmnt")

            displayNoneToggle(prvSlctdNav);
            // if (document.querySelector("#adaptive-nav-wrap").classList.contains("opened") && document.querySelector(".drp-dwn-active-lmnt")){
            //     document.querySelector(".drp-dwn-active-lmnt").classList.add("display-none");
            // }
        }
        //clicking into a drop down with no others open
        if(e.target.parentElement.classList.contains("unselected")){
            displayNoneToggle(e.target.parentElement.parentElement.parentElement.children[1]);
            prvSlctdNav = e.target.parentElement.parentElement.parentElement.children[1];
            prvSlctdNav.classList.add("drp-dwn-active-lmnt")
        }
        //clicking out of drop down (so clicking the drop down thats currently active)
        else if(e.target.parentElement.classList.contains("selected")){
            displayNoneToggle(e.target.parentElement.parentElement.parentElement.children[1]);
            //if(document.querySelector("#adaptive-nav-wrap").classList.contains("opened")) prvSlctdNav.classList.remove("drp-dwn-active-lmnt");
            if(document.querySelector("#adaptive-nav-wrap").classList.contains("closed") || !document.querySelector(".selectedImportant")) prvSlctdNav = '';
        }
    }
    // else if (e.target.innerText == "Audio" && e.target.parentElement.classList.contains("unselected")){
    //     prvSlctdNav = e.target.parentElement.classList.contains("unselected")
    // }
}

//code for dsktpDrpDwnFunct
function dsktpDrpDwnCode(e){
    var navWrap = document.querySelector("#dsktp-nav-lst");
    let selectedDrpDwn = e.target.parentElement.parentElement.children[1];

    switch(e.target.innerText == "Audio"){
        case false: {
            
            //if drop down is not currently active
            if(!navWrap.classList.contains("drp-dwn-active")){
                selectedDrpDwn.classList.remove("display-none");
                navWrap.classList.add("drp-dwn-active");
                prvSlctdNav = e.target.parentElement.parentElement.children[1];
            }
            //if drop down is currently active 
            else if(navWrap.classList.contains("drp-dwn-active")) {
                
                // prvSlctdNav.classList.add("display-none");
                if(selectedDrpDwn.classList.contains("display-none") && prvSlctdNav!==selectedDrpDwn){
                    selectedDrpDwn.classList.remove("display-none");
                    prvSlctdNav.classList.add("display-none");
                    prvSlctdNav = e.target.parentElement.parentElement.children[1];
                }
                //if user is clicking out of selected nav
                else { 
                    selectedDrpDwn.classList.add("display-none");
                    navWrap.classList.remove("drp-dwn-active");
                    //prvSlctdNav = e.target.parentElement.parentElement.children[1];
                }
            }
            break;
        }
        case true: {
            if (prvSlctdNav !== ""){
                prvSlctdNav.classList.add("display-none");
                navWrap.classList.remove("drp-dwn-active");
            }
            break;
        }
    }
}

export{adptvTabCode}
export {dsktpDrpDwnCode}