import React from 'react'
import { sineWaveLowsNode,sineWaveMidsNode,sineWaveHighsNode} from './Audio_Functions/Audio_Sg_Init'

class Audio_Range extends React.Component {
    constructor(props){
        super(props);
        this.state = { }
    }
    componentWillMount(){
        //initally attach jquery CDN - as the audio range uses jquery
        const scriptOne = document.createElement("script");
        scriptOne.src = "https://ajax.googleapis.com/ajax/libs/jquery/1.12.4/jquery.min.js";
        document.body.appendChild(scriptOne);
    }
    componentDidMount(){
        
        //attach jquery-UI CDN, which needs initial jquery CDN to be loaded first (this allows slider manipulation)
        const scriptTwo = document.createElement("script");
        scriptTwo.src = "https://ajax.googleapis.com/ajax/libs/jqueryui/1.10.2/jquery-ui.min.js";
        document.body.appendChild(scriptTwo);

        //attach custom external script for audio range control/final design after both jquery CDNs have loaded and react page has loaded
        const scriptThree = document.createElement("script");
        scriptThree.src = "https://koalacode.com.au/External_Scripts/Audio_Range_Script.js";
        document.body.appendChild(scriptThree); //scriptThree.setAttribute("defer", "defer");
    }

    render() {

      return (
        //Audio tools wrap
        <div>
          <div className="audio-range">
            <div class="slider green"></div>
            <div class="slider green display-none"></div>
          </div>
        </div>
      );
    }
}


window.addEventListener("load", () => {
    var masterAudioNode = document.querySelector(".mstr-audio-node");
    
    //set volumes of audio nodes to 50%
        [masterAudioNode,sineWaveHighsNode,sineWaveMidsNode,sineWaveLowsNode].forEach( (node) => {
            node.volume = 1
        });

    //function that updates volume when volume is being interacted with
        var liveVolFunct = ''; 
        function updtVol(){
            const rngHndl = document.querySelector(".ui-slider-handle");
            liveVolFunct = setInterval(() => {
                var liveVolume = parseInt(rngHndl.style.left)/100;
                [masterAudioNode,sineWaveHighsNode,sineWaveMidsNode,sineWaveLowsNode].forEach( (node) => {
                    node.volume = liveVolume;
                }); 
            }, 20);
        }

    //Event listeners for updtVol
        //when audiorange handle is clicked, set the volume of all audio elements (eg .mstr-audio-node) as per the range handle
        const range = document.querySelector(".slider.green");

        //DESKTOP
        range.addEventListener("mousedown", (e) => {

            //as long as the user has their mouse down on rngHndl, update the volume
            updtVol();
        });
        //when users pulls mouse off range handle, stop updating volume
        range.addEventListener("mouseup",() => {
            clearInterval(liveVolFunct);
        });

        //RESPONSIVE
        range.addEventListener("touchstart", (e) => {
            
            //when user touches the range, update the volume accordingly
            updtVol();
        });
        //when users puls finger off range, stop updating volume
        range.addEventListener("touchend",() => {
            clearInterval(liveVolFunct);
        });
});




  export default Audio_Range