//PARENT NAV BAR
    class ParentNavBar extends React.Component {
        constructor(props){
            super(props);
            this.state = { }
            this.navClickFunct = (e) => {
                if (window.innerWidth < 1000){
                    this.adptvTabFunct(e);
                }
                else {
                    this.dsktpDrpDwnFunct(e);
                }
            }
        //ADAPTIVE FUNCTION (open close adaptive tab function)
            this.adptvTabFunct = (e) => {
                adptvTabCode(e);
                if (e.target.parentElement.classList.contains("main-nav-link")) updateBrowsingState(e);
            }
        //DESKTOP DROPDOWN FUNCTION (show drop down in desktop mode)
            this.dsktpDrpDwnFunct = (e) => { 
                dsktpDrpDwnCode(e);
                updateBrowsingState(e);
            }
        }
        //initial object that tracks navigation (curNavState)
        componentDidMount(){
            initCurNavState();
        }

        render(){
            return (
                
                <div>
                    <div className="adptv-nav-tab tb-cls-trgr" onClick={(e) => this.navClickFunct(e)}></div>
                    <div id="main-nav-wrap">
                        <nav id="desktop-nav" style={{position: "relative",padding: "0px 20px"}}>
                            <div className="rSZoid"></div>
                            <div className="rSZoid" style={{left: "-80px", top: "0"}}></div>
                            <div className="rBZoid"></div>
                            <div className="rBZoid" style={{left: "-60px", top: "0", transform: "rotateY(180deg)"}}></div>

                            <ul id="dsktp-nav-lst" style={{display: "flex"}}>
                                <MainNavChild type="dsktp" toolTitle="Audio" fontSize="2.3rem" dropDown="false" onClick={(e) => this.navClickFunct(e)}/>  
                                <MainNavChild type="dsktp" toolTitle="Vision" fontSize="2.3rem" dropDown="true" onClick={(e) =>this.navClickFunct(e)}/>
                                <MainNavChild type="dsktp" toolTitle="Audio &#38; Vision" extra="position: relative; top: 3.3px;" fontSize="1.5rem" dropDown="true" onClick={(e) =>this.navClickFunct(e)}/>
                                <MainNavChild type="dsktp" toolTitle="Info" fontSize="2.3rem" dropDown="true" onClick={(e) =>this.navClickFunct(e)}/>
                            </ul>
                        </nav>
                    </div>
                    <div id="adaptive-nav-wrap" className="closed" ref={this.adptvNavWrap} style={{zIndex: "-100;",visibility: "hidden"}}>
                        <nav id="adaptive-nav">
                            <ul id="adptv-nav-lst" style={{display: "flex"}}>
                                <MainNavChild type="adptv" tabClose="tb-cls-trgr" toolTitle="Audio" fontSize="2rem" onClick={(e) => this.navClickFunct(e)}/>
                                <MainNavChild type="adptv" tabClose="tb-cls-trgr" toolTitle="Vision" fontSize="2rem" onClick={(e) => this.navClickFunct(e)}/>
                                <MainNavChild type="adptv" tabClose="tb-cls-trgr" toolTitle="Audio &#38; Vision" style={{position: "relative;", top: "3.3px"}}fontSize="1.2rem" onClick={(e) => this.navClickFunct(e)}/>
                                <MainNavChild type="adptv" tabClose="tb-cls-trgr" toolTitle="Info" fontSize="2rem" onClick={(e) => this.navClickFunct(e)}/>
                            </ul>
                        </nav>
                    </div>
                </div>
            );
        }
    }

  //PARENT NAV CHILDREN (Nav options within the desktop (main) navigation)
  class MainNavChild extends React.Component {
    constructor(props){
        super(props);
        this.state = { }
    }
    render() {

    //function that returns an array of the options into dropDownNav depending on the navs toolTitle
    const ConfigNavTools = () => {
        if (this.props.toolTitle === "Vision"){
            return <DropdownNav optionOne="Greyscale" optionTwo="RGB Pattern" optionThree="Solid Color"/>
        }
        else if (this.props.toolTitle === "Audio & Vision"){
            return <DropdownNav optionOne="AV Sync Test" optionTwo="Frequency Sweep" optionThree="Music Video" OptionFour="Left Right Test" />
        }
        else if (this.props.toolTitle === "Info"){
            return <DropdownNav optionOne="Instructions" optionTwo="About" /*optionThree="Privacy Policy"*//>
        }
    }

    //Configure the dropdowns for main
        let insertDropDown;
        if (this.props.dropDown === "true"){
            insertDropDown = ConfigNavTools()
        }
        //configure the dropdowns for adaptive 
        let adptvDropDown;
        if (this.props.type == "adptv" && this.props.toolTitle!="Audio"){
            adptvDropDown = ConfigNavTools();
        }

        const childStyles = {
            alignItems: "center"    ,
            display: "flex",
            flexDirection: "column",
            fontSize: this.props.fontSize,
            height: "40px",
            justifyContent: (!this.props.toolTitle == "Audio&Vision") ? "center" : "flex-end"
               
            // width: "150px",
        }

        return (
            <div className="navChildWrap" >
                <li style={childStyles} > 
                    <li className={"main-nav-link unselected " + noSpace(this.props.toolTitle)} onClick={(e) => makeSelectedNav(e)}>
                        <span className={"tool-name"} onClick={this.props.onClick}>{this.props.toolTitle}</span>
                    </li>
                    {insertDropDown}
                </li>
                {adptvDropDown}
            </div>
        );
    }  
}

export { ParentNavBar } ;