    //MAKE ETARGET
    //make eTarget var for when the focus elmnt is a wrap with children
    function makeETarget(e){
        if (e.target.classList.contains("wrap")){ return e.target }
        else if (e.target.parentElement.classList.contains("wrap")){ return e.target.parentElement }
        else if (e.target.parentElement.parentElement.classList.contains("wrap")){ return e.target.parentElement.parentElement }
        else if (e.target.parentElement.parentElement.parentElement.classList.contains("wrap")){ return e.target.parentElement.parentElement.parentElement }
        else if (e.target.parentElement.parentElement.parentElement.parentElement.classList.contains("wrap")){ return e.target.parentElement.parentElement.parentElement.parentElement }
    }

//.................................................................
    //TOGGLE DISPLAY NONE
    function displayNoneToggle(element){
        if (!element.classList.contains("display-none")){
            element.classList.add("display-none");
        }
        else if (element.classList.contains("display-none")){
            element.classList.remove("display-none");
        }
    } export {displayNoneToggle}
