    //REMOVE SPACES FROM STRING
    function noSpace(x){
        var firstArray = [];
        var noStringArray = "";
        
        for (var i = 0; i < x.length; i++){
            firstArray.push(x[i]);
            if (firstArray[i] === " "){
                delete firstArray[i];
            } 
        }
        
        noStringArray = firstArray.toString();
        var oldWord = noStringArray;
        var newWord = "";
        
        for (var i = 0; i < oldWord.length; i++){
            if (oldWord[i] === ","){
        
            }
            else{
                    newWord = newWord + oldWord[i];
                }
        }
        
        return newWord;
    } export {noSpace}
//.................................................................
    //UNSELECTED => SELECTED FUNCTION
    var slctdParentNav = ''; // pertains to the drop down wrap of the selected nav (eg visions clicked drop down wrap) (originally selectedDropDown)
    var slctdChildNav = ''; //child nav of the parent nav (rgb of vision) 
    const makeSelectedNav = (e) => {

        var curLmnt = (() => {
            return e.target.classList.contains("drp-dwn-child") ? e.target : e.target.parentElement;  
        })();

        switch(curLmnt){
            //IF TARGET IS NAV CHILD
            case e.target: {
                 
                //if user is clicking on a nav btn and that btn isn't currently selected
                if (curLmnt.classList.contains("unselected")){
                    if (slctdChildNav != ''){ //so this is not the first time a tab has been clicked
                        slctdChildNav.classList.remove("selected");
                        slctdChildNav.classList.add("unselected");
                    }
                    slctdChildNav = curLmnt;
                    slctdChildNav.classList.add("selected"); 
                    slctdChildNav.classList.remove("unselected"); 
                }

                //force ".selected" styling onto the nav that currently has a child selected
                if (document.querySelector(".selectedImportant") !== null){
                    document.querySelector(".selectedImportant").classList.remove("selectedImportant");
                }

                slctdParentNav.classList.add("selectedImportant");

                //THOUGHT I NEEDED THIS AT THE TIME
                //if in adaptive mode, close tab once nav child has been clicked
                // if(document.querySelector("#adaptive-nav-wrap").classList.contains("open")){
                //     document.querySelector("#adaptive-nav-wrap").style = "";
                //     document.querySelector("#adaptive-nav-wrap").classList.remove("opened");
                //     document.querySelector("#adaptive-nav-wrap").classList.add("closed");
                // }

                // *SPECIAL* if navchild clicked, close adaptive tab and hide drp-dwn-active-lmnt
                if(document.querySelector("#adaptive-nav-wrap").classList.contains("opened")){
                    document.querySelector(".drp-dwn-active-lmnt").classList.add("display-none");
                    document.querySelector("#adaptive-nav-wrap").classList.remove("opened");
                    document.querySelector("#adaptive-nav-wrap").classList.add("closed")
                }

                break;
            }
            //IF TARGET IS NAV PARENT
            case e.target.parentElement: {
                
                //if(!curLmnt.classList.contains("Audio")) { e.target.parentElement.parentElement.children[1].classList.add("drp-dwn-active-lmnt"); }
                var skip = false; // used to skip over subsqeuent if statmements if Audio btn is clicked

                //if clicking audio btn and another nav had been selected previously
                if(curLmnt.classList.contains("Audio") && curLmnt.classList.contains("unselected") && slctdChildNav != ''){
                    slctdChildNav.classList.remove("selected");
                    slctdChildNav.classList.add("unselected");
                    if(document.querySelector(".selectedImportant") != null){
                        document.querySelector(".selectedImportant").classList.remove("selectedImportant");
                    }
                    curLmnt.classList.add("selectedImportant");
                }
                //if audio is already selected an is clicked again
                else if(curLmnt.classList.contains("Audio") && curLmnt.classList.contains("selected")){
                    // ["selectedImportant","selected"].forEach( item => curLmnt.classList.remove(item));
                    // curLmnt.classList.add("unselected");    
                    skip = true;
                }
//HERHERHERHE
                //if user is clicking on a nav btn and that btn isn't currently selected
                if (curLmnt.classList.contains("unselected") && !skip){
                    //if another another nav child was selected previously
                    if(document.querySelector(".drp-dwn-active-lmnt")) document.querySelector(".drp-dwn-active-lmnt").classList.remove("drp-dwn-active-lmnt");
                    //if curLmnt is not audio nav
                    if(!curLmnt.classList.contains("Audio")) {
                        
                        //depending whether or not use is on a responsive device
                        //e.target.parentElement.parentElement.children[1].classList.add("drp-dwn-active-lmnt") (bottom code original)
                        (document.querySelector("#adaptive-nav-wrap").classList.contains("closed")) ? 
                        e.target.parentElement.parentElement.children[1].classList.add("drp-dwn-active-lmnt") :
                        e.target.parentElement.parentElement.parentElement.children[1].classList.add("drp-dwn-active-lmnt");
                    }
                     //so this is not the first time a tab has been clicked
                    if (slctdParentNav != ''){
                        slctdParentNav.classList.remove("selected");
                        slctdParentNav.classList.add("unselected");
                    }
                    slctdParentNav = curLmnt;
                    slctdParentNav.classList.add("selected"); 
                    slctdParentNav.classList.remove("unselected");
                    if(curLmnt.classList.contains("Audio")){
                        curLmnt.classList.add("selectedImportant");
                    }
                }
                //user clicking out of currently selected option without a selected nav child
                else if (curLmnt.classList.contains("selected") && !curLmnt.classList.contains("selectedImportant") && !skip){
                    
                    if(!curLmnt.classList.contains("Audio")) {

                        //depending whether or not user is on a responsive device
                        (window.innerWidth > 1000) ? 
                        e.target.parentElement.parentElement.children[1].classList.remove("drp-dwn-active-lmnt") : //desktop
                        e.target.parentElement.parentElement.parentElement.children[1].classList.remove("drp-dwn-active-lmnt"); //responsive
                    }
                    slctdParentNav.classList.remove("selected");
                    slctdParentNav.classList.add("unselected");
//RIGHT HERERE
                    //if .selectedImportant doesn't contain selected, add .selected
                    if (document.querySelector(".selectedImportant") && document.querySelector(".selectedImportant").classList.contains("unselected")){
                        document.querySelector(".selectedImportant").classList.remove("unselected");
                        document.querySelector(".selectedImportant").classList.add("selected");
                        //change slctdParentNav back to orginally selected parent Nav
                        slctdParentNav = document.querySelector(".selectedImportant");
                    }
                }
                //if nav child has previously been selected and its tab closed and then opened again or vice versa 
                else if (curLmnt.classList.contains("selectedImportant") && document.querySelector(".drp-dwn-child.selected")){
                    // opened -> closed
                    if (document.querySelector(".drp-dwn-active-lmnt.display-none")){
                        document.querySelector(".drp-dwn-active-lmnt.display-none").classList.remove("drp-dwn-active-lmnt");
                    }
                    //closed -> opened
                    else{
                        (document.querySelector("#adaptive-nav-wrap").classList.contains("closed")) ? 
                        curLmnt.parentElement.children[1].classList.add("drp-dwn-active-lmnt") :  //desktop code
                        curLmnt.parentElement.parentElement.children[1].classList.add("drp-dwn-active-lmnt");   
                    }
                }
                break;
            }
        }

    }

