import React from 'react'
import '../Styles/globalStyles.scss'



//TO DO:
// if there is an element that has a child nav selected and user is currently browsing in a different nav and logo pressed and unpressed
    //error
    //you need to keep activelement on when there is an active tool 
    //look into this more deply

class Logo extends React.Component {
    constructor(props){
        super(props);
        this.state = {

        }
    }

    render() {

        //remove selected styling and revert back to page default
        const resetIndex = () => {

            //if only Audio has been selected
            if(document.querySelector(".Audio").classList.contains("selected") && !document.querySelector(".drp-dwn-active-lmnt")){

                //remove selected styling from nav parent
                document.querySelector(".selected").classList.add("unselected");
                ["selectedImportant","selected",].forEach( item => document.querySelector(".selected").classList.remove(item));
            }
            //if Audio has been selected and user is browsing another nav
            else if (document.querySelector(".Audio.selectedImportant") && document.querySelector(".drp-dwn-active-lmnt")){
                document.querySelector(".drp-dwn-active-lmnt").classList.add("display-none");   

                document.querySelectorAll(".selected").forEach( (item,index) => {
                    item.classList.add("unselected");
                    item.classList.remove("selected");
                } );

                document.querySelector(".selectedImportant").classList.remove("selectedImportant");
            }
            //if only parent nav has been selected which isn't audio(eg vision)
            else if (document.querySelector(".selected") && !document.querySelector(".selectedImportant") && document.querySelector(".Audio").classList.contains("unselected")){

                //remove selected styling from nav parent
                document.querySelector(".selected").classList.add("unselected");
                ["selected",].forEach( (item) => document.querySelector(".selected").classList.remove(item));

                //notify page this drp-dwn is no longer active
                document.querySelector(".drp-dwn-active-lmnt").classList.add("display-none");
                document.querySelector(".drp-dwn-active-lmnt").classList.remove("drp-dwn-active-lmnt");
                if(document.querySelector(".drp-dwn-active")) document.querySelector(".drp-dwn-active").classList.remove("drp-dwn-active");
            }
            //if a drop down nav child has been selected (eg rgb of vision) and tab is either opened or closed
            else if(document.querySelector(".selectedImportant") && document.querySelectorAll(".selected").length == 1){

                //remove selected styling from nav parent
                document.querySelector(".selected").classList.add("unselected"); 
                ["selectedImportant","selected",].forEach( item => document.querySelector(".selected").classList.remove(item)); 
                if (document.querySelector(".drp-dwn-active-lmnt")) document.querySelector(".drp-dwn-active-lmnt").classList.add("display-none");
                
                //notify page this drp-dwn is no longer active
                if(document.querySelector(".drp-dwn-active-lmnt")) document.querySelector(".drp-dwn-active-lmnt").classList.remove("drp-dwn-active-lmnt");
                if(document.querySelector(".drp-dwn-active")) document.querySelector(".drp-dwn-active").classList.remove("drp-dwn-active");

                //remove styling from selected nav child (eg rgb of vision)
                if(document.querySelector(".drp-dwn-child.selected")) document.querySelector(".drp-dwn-child.selected").classList.add("unselected");
                if(document.querySelector(".drp-dwn-child.selected")) document.querySelector(".drp-dwn-child.selected").classList.remove("selected");
            }
            //if there is an element that has a child nav selected and user is currently browsing in a different nav 
            else if(document.querySelectorAll(".selected").length == 2 && document.querySelector(".selectedImportant")) {

                //hide active drp-dwn
                if(document.querySelector(".drp-dwn-active-lmnt"))  document.querySelector(".drp-dwn-active-lmnt").classList.add("display-none");                

                //remove .selected from child nav and nav that doesnt have a child active
                document.querySelectorAll(".selected").forEach( (item,index) => {
                    item.classList.add("unselected");
                    item.classList.remove("selected");
                } );

                //remove .drp-dwn related classes
                if(document.querySelector(".drp-dwn-active-lmnt")) document.querySelector(".drp-dwn-active-lmnt").classList.remove("drp-dwn-active-lmnt");
                if(document.querySelector(".drp-dwn-active")) document.querySelector(".drp-dwn-active").classList.remove("drp-dwn-active");

                //remove .selectedImportant 
                document.querySelector(".selectedImportant").classList.remove("selectedImportant")
            }
            //additional code for responsive mode
            if (document.querySelector("#adaptive-nav-wrap").classList.contains("opened")){
                document.querySelector("#adaptive-nav-wrap").style = '';
            }
        }  
        return (
            <div className="z500 pointer" id="site-logo" onClick={() => resetIndex()}>
                <span>AV</span>
                <br></br>
                <span>Tester</span>
            </div>
        );
    }
  }



  export { Logo } ;