import React from 'react'
import {fullscreenFunct} from '../../Tools/codeTools'

var freeze = false;
class Vision_Solid extends React.Component {
    constructor(props){
        super(props);
        this.state = { }
    }

    componentDidMount(){
    //DOUBLE CLICK FULLSCREEN 
        //dlb click to toggle full screen mode on/off 
        var fsConfig = { state: "off" }
        console.log("dbl click method" + fsConfig.state)
        var fsMdeTrgr = '';
        document.querySelector(".solid-node").addEventListener("mousedown", () => {
          
          //mousedown, once down for 2 seconds, enter full screen mode, freeze = true so color doesnt change, 500ms later freeze = false
          var timer = 0;
          fsMdeTrgr = setInterval(() => {
            timer++
            if(timer == 3) { fsFunction(); freeze = true;}
          }, 500);

            // setTimeout(() => {
            //   freeze = false;
            // }, 500);

          debugger;
          function fsFunction(){
            if (fsConfig.state == "off") {
              fullscreenFunct(document.querySelector(".solid-node")); fsConfig.state = "on";

              //once user has entered fs mode, constantly check for if the user has exited fullscreen mode, if so  set fsConfig.state = "off"
              setTimeout(() => {
                debugger;
                var ifFsExited = setInterval(() => {
                  debugger;
                  if(window.innerHeight != window.screen.height) {
                    fsConfig.state = "off"; 
                    clearInterval(ifFsExited)
                  } 
                }, 20);
              }, 5000);

            } //turn fs on if its off 
            else if (fsConfig.state =="on") { fullscreenFunct(document.querySelector(".solid-node")) /*fsConfig.state = "off"*/ } //turn fs off if its on
        }
        });

        document.querySelector(".solid-node").addEventListener("mouseup", () => {
          clearInterval(fsMdeTrgr);
          setTimeout(() => { if(freeze == true) freeze = false; }, 500);
        });
    }


    render() {
    //COLOR TOGGLE
      //each "click" will show a new colour by sorting through an array, using an integret, when it hits a certain number, the integer goes back to 0 
      
      //solidClrNode.addEventListener("click", solidClrSort ) 
          var colorArray = ["yellow","cyan","lightgreen","pink","red","purple","grey","black","white"];
          var cntr = 0;



      //change color by selecting an array item with current counter value, once counter exceeds the colorArray.length, reset counter to 0
      var dblClickCntr = 0;
      //var freeze = false;
      var prvDbl = 0;

      if(document.querySelector(".solid-node")){

      }

      function solidClrSort(){
   
        if(/*userDblClkd != true && */ freeze == false /* && hasBeenDblClkd == false*/ ){
          document.querySelector(".solid-node").style.background = colorArray[cntr];
          cntr++
          if(cntr >= colorArray.length) cntr = 0; // so once colorArray becomes +1 of the colorArray.length, reset cntr
        }

      }

      return (
          <div>
              <div className="vision-solid-wrap tool-element-wrap display-none">
                <div className="solid-node" onClick={() => solidClrSort()} style={{backgroundColor: "white", /*zIndex: 1000*/} }></div>
              </div>
          </div>
      );
    }
  }



  export {Vision_Solid}